rcle */\n  transform: scale(0);\n  transition: 120ms transform ease-in-out;\n  background-color: var(--accent-color);\n  border-radius: 50%;\n}\n\n.checkbox-container input[type=\"checkbox\"]:checked::before {\n  transform: scale(1);\n}\n\n.top-row {\n  grid-column: 2 / 3;\n  grid-row: 1 / 2;\n  padding-bottom: 8px;\n  display: flex;\n  justify-content: start;\n}\n\n.title {\n  font-size: 1.2rem;\n  font-weight: 500;\n}\n\n.top-row button {\n  background: none;\n  border: none;\n  align-self: flex-start;\n}\n\n.copy-todo-btn {\n  margin-left: auto;\n  margin-right: 4px;\n}\n\n.description {\n  grid-column: 2 / 3;\n  grid-row: 2 / 3;\n  /* overflow: hidden; */\n  /* text-overflow: ellipsis; */\n  /* text-wrap: nowrap; */\n  margin-bottom: 8px;\n  color: var(--menu-option-highlight-color);\n  font-size: 0.8rem;\n}\n\n.bottom-bar {\n  grid-column: 2 / 3;\n  display: flex;\n  justify-content: space-between;\n}\n\n.date {\n  font-size: 0.8rem;\n  display: flex;\n  align-items: center;\n  gap: 4px;\n}\n\n.priority {\n  font-size: 0.8rem;\n  display: flex;\n  align-items: center;\n  color: var(--accent-color);\n}\n\n.priority span,\n.date span {\n  font-size: 1rem;\n  /* change the size of icons */\n}\n\n/* Styles for Dialog Box */\n\n#create-todo-dialog,\n#edit-todo-dialog,\n#rename-project-dialog {\n  background-color: var(--primary);\n  margin: auto;\n  border: none;\n  border-radius: 16px;\n  font-size: 1rem;\n  padding: 16px;\n  width: calc(20vw + 100px);\n}\n\n#create-todo-dialog .content,\n#edit-todo-dialog .content,\n#rename-project-dialog .content{\n  display: flex;\n  flex-direction: column;\n  gap: 8px;\n}\n\n::backdrop {\n  background-image: linear-gradient(45deg, var(--primary), var(--secondary));\n  opacity: 0.45;\n}\n\n.form-input {\n  margin-bottom: 4px;\n  display: flex;\n  flex-direction: column;\n}\n\nform label,\nform input,\nform textarea {\n  display: block;\n  width: 100%;\n}\n\nform input,\nform textarea,\nform select {\n  padding: 4px;\n}\n\nform t